}

.avatar {
    -webkit-animation-name: 
        fadeInSlow,
        growAndShrink;
            animation-name: 
        fadeInSlow,
        growAndShrink;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
} 

@-webkit-keyframes fadeInSlow {
    0% { opacity: 0; }
    95% { opacity: 0.95; }
    100% { opacity: 1; }
} 

@keyframes fadeInSlow {
    0% { opacity: 0; }
    95% { opacity: 0.95; }
    100% { opacity: 1; }
}

@-webkit-keyframes growAndShrink {
    0% { -webkit-transform: scale(1); }
    80% { -webkit-transform: scale(1.3); }
    90% { -webkit-transform: scale(0.9); }
    100% { -webkit-transform: scale(1); }
}

.revealCursive {
    overflow: hidden;               /* Ensures the content is not revealed until the animation */
    white-space: wrap;              /* Keeps the content on a single line */
    
    animation-name: reveal;
    -webkit-animation-name: reveal;

    animation-duration: 5s;
    -webkit-animation-duration: 5s;

    animation-timing-function: steps(30, end);
    -webkit-animation-timing-function: steps(30, end);


    max-width: -webkit-max-content;


    max-width: -moz-max-content;


    max-width: max-content;         /* Needed on Chrome and FireFox to hide "of" while typing "Master" */
}

@-webkit-keyframes reveal {
    0% { width: 0 }
    50% { width: 0 }
    100% { width: 100% }
}

@keyframes reveal {
    0% { width: 0 }
    50% { width: 0 }
    100% { width: 100% }
}

.revealO {
    -webkit-animation-name: fadeIn;
            animation-name: fadeIn;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; 
    visibility: hidden;
}


.revealF {
    -webkit-animation-name: fadeIn;
            animation-name: fadeIn;
    -webkit-animation-duration: 4.35s;
            animation-duration: 4.35s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; 
    visibility: hidden;
}


/* Stamping doesn't work super well in Edge */
#stampContainer {
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    width: 270px;
    position: relative;

}

#stamp, #stampPrint {
    width: 100%;
    position: absolute;
}

#stampPrint {
    animation-name: getStamped;
    -webkit-animation-name: getStamped;

    animation-duration: 3s;
    -webkit-animation-duration: 3s;

    animation-fill-mode: forwards; 
    -webkit-animation-fill-mode: forwards;

    animation-delay: 6.3s;
    -webkit-animation-delay: 6s;

    opacity: 0;
}

@-webkit-keyframes getStamped {
/* @-webkit-keyframes getStamped { */       /* keyframes type doesn't seem to matter */
    0% {
        opacity: 0;
    }
    10% {
        opacity: 0.50;
        color: #b596c5;
    }
    30% {
        opacity: 1;
        color: #b596c5;
    }
    60% {
        opacity: 1;
        color: white;
    }
    100% {
        opacity: 1;
        color: white;
    }
}

@keyframes getStamped {
/* @-webkit-keyframes getStamped { */       /* keyframes type doesn't seem to matter */
    0% {
        opacity: 0;
    }
    10% {
        opacity: 0.50;
        color: #b596c5;
    }
    30% {
        opacity: 1;
        color: #b596c5;
    }
    60% {
        opacity: 1;
        color: white;
    }
    100% {
        opacity: 1;
        color: white;
    }
}
#stamp {
    z-index: 10;
}

.stamp {
    animation-name: stamping;
    -webkit-animation-name: stamping;

    animation-delay: 6s;
    -webkit-animation-delay: 6s;

    animation-duration: 1s;
    -webkit-animation-duration: 1s;

    animation-fill-mode: forwards;
    -webkit-animation-fill-mode: forwards;

    opacity: 0;
}

@-webkit-keyframes stamping {
/* @-webkit-keyframes stamping { */         /* keyframes type doesn't seem to matter */
    0% {
        opacity: 0;
    }
    10% {
        opacity: 0.50;
        color: #b596c5;
        border-style: solid;
        border-radius: 10%;
        border-width: 4px;
        border-color: #b596c5;
        -webkit-transform-origin: 50% 50%;
                transform-origin: 50% 50%;
        -webkit-transform: rotate(-2deg) scale(3.5);
                transform: rotate(-2deg) scale(3.5);
        transition: all .3s cubic-bezier(0.6, 0.04, 0.7, 0.5);
    }
    30% {
        opacity: 1;
        color: #b596c5;
        border-style: solid;
        border-radius: 10%;
        border-width: 4px;
        border-color: #b596c5;
        -webkit-transform: rotate(-10deg) scale(1);
                transform: rotate(-10deg) scale(1);
    }
    65% {
        opacity: 1;
        color: #b596c5;
        border-style: solid;
        border-radius: 10%;
        border-width: 4px;
        border-color: #b596c5;
        -webkit-transform: rotate(5deg) scale(1.3);
                transform: rotate(5deg) scale(1.3);
    }
    100% {
        opacity: 0;
        -webkit-transform: rotate(0deg) scale(1);
                transform: rotate(0deg) scale(1);
        color: white;
        border-color: #b596c5;
        border-style: none;
    }
}

@keyframes stamping {
/* @-webkit-keyframes stamping { */         /* keyframes type doesn't seem to matter */
    0% {
        opacity: 0;
    }
    10% {
        opacity: 0.50;
        color: #b596c5;
        border-style: solid;
        border-radius: 10%;
        border-width: 4px;
        border-color: #b596c5;
        -webkit-transform-origin: 50% 50%;
                transform-origin: 50% 50%;
        -webkit-transform: rotate(-2deg) scale(3.5);
                transform: rotate(-2deg) scale(3.5);
        transition: all .3s cubic-bezier(0.6, 0.04, 0.7, 0.5);
    }
    30% {
        opacity: 1;
        color: #b596c5;
        border-style: solid;
        border-radius: 10%;
        border-width: 4px;
        border-color: #b596c5;
        -webkit-transform: rotate(-10deg) scale(1);
                transform: rotate(-10deg) scale(1);
    }
    65% {
        opacity: 1;
        color: #b596c5;
        border-style: solid;
        border-radius: 10%;
        border-width: 4px;
        border-color: #b596c5;
        -webkit-transform: rotate(5deg) scale(1.3);
                transform: rotate(5deg) scale(1.3);
    }
    100% {
        opacity: 0;
        -webkit-transform: rotate(0deg) scale(1);
                transform: rotate(0deg) scale(1);
        color: white;
        border-color: #b596c5;
        border-style: none;
    }
}

.avatarContainer {
    -webkit-animation-name: moveUpAvatar;
            animation-name: moveUpAvatar;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-delay: 7.5s;
            animation-delay: 7.5s;
}

@-webkit-keyframes moveUpAvatar {
    50% { -webkit-transform: translateY(0px); transform: translateY(0px); }
    100% { -webkit-transform: translateY(-90px); transform: translateY(-90px); }
}

@keyframes moveUpAvatar {
    50% { -webkit-transform: translateY(0px); transform: translateY(0px); }
    100% { -webkit-transform: translateY(-90px); transform: translateY(-90px); }
}

.title {
