    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-delay: 7.5s;
            animation-delay: 7.5s;
}

@-webkit-keyframes moveUpLeftTitle {
    50% { -webkit-transform: translateX(0px) translateY(0px); transform: translateX(0px) translateY(0px); }
    100% { -webkit-transform: translateX(-340px) translateY(-180px); transform: translateX(-340px) translateY(-180px); }
}

@keyframes moveUpLeftTitle {
    50% { -webkit-transform: translateX(0px) translateY(0px); transform: translateX(0px) translateY(0px); }
    100% { -webkit-transform: translateX(-340px) translateY(-180px); transform: translateX(-340px) translateY(-180px); }
}

.subtitle {
