.tilesContainer {
    overflow: scroll;
    display: -webkit-flex;
    display: flex;
    padding-left: 50px;
    padding-right: 50px;
    padding-top: 10px;
    padding-bottom: 160px;
    width: 87.5%;
    position: relative;
    left: 50px;
    top: 100px;
    -webkit-animation: 13.5s fadeIn;
            animation: 13.5s fadeIn;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; 
    visibility: hidden;


    /* Hide tiles scrollbar */
    scrollbar-width: none;      /* Firefox */
    -ms-overflow-style: none;   /* IE 10+ */

}

/* Hide general scrollbar */
html {
    scrollbar-width: none;          /* Firefox */
    -ms-overflow-style: none;       /* IE 10+ */
}

/* Hide a;; scrollbars in Chrome and Mac (?) */
::-webkit-scrollbar { 
    display: none; 
}

.tile {
    position: relative;
    overflow: visible;
    /* display: inline-block; */
    top:-10px;
    left:-5px;
}

